.App {
  font-family: sans-serif;
}

section {
  height: 100vh;
  padding-top: 8vh;
  padding-bottom: 8vh;
  scroll-snap-align: start;
  background-color:#242424;
  color: white;
  text-align: center;
  position: relative;
}

.Profile-big {
  height: 40vmin;
  border-radius: 40vmin;
  border: solid 4px white;
}

.Button-test {
  cursor: pointer;
  width:150px;
  height:30px;
  position: absolute;
  bottom: 20vh;
  left: 50%;
  margin-left: -75px;
  border: 3px white solid;
  border-radius: 5px;
  font-weight: 600;
  background: linear-gradient(to bottom, #ff7b00 50%, #242424 50%);
  background-size: 100% 200%;
  background-position: bottom;
  transition: all .35s ease-out;
}

.Button-test:hover {
  background-position: top;
  color: #242424;
  transition: all .35s ease-out;
 }

 .Button-test .Arrow {
  font-size: 18px;
  font-weight: 1000;
  margin-top: 3px;
  display: inline-block;
  position: relative;
  left: 10px;
  top:3px;
 } 

 .Button-test:hover .Arrow {
  color: #242424;
  transition: all .5s ease-out;
  transform: rotateX(360deg);
 } 

@media (max-width: 600px) {
  .Button-test {
    bottom: 32vh;
  }
}
