.Header-component {
    height: 8vh;
    background-color:#242424;
    text-align: right;
    position: sticky;
    top: 0;
    z-index: 100;
}

.Tool-button {
    color: white;
    background-color: transparent;
    border: none;
    font-weight: bold;
    font-size: 18px;
    margin-top: 2vh;
    margin-right: 25px;
    display: inline-block;
}

.Tool-button:hover {
    cursor:pointer;
   }

.active {
    border-bottom: solid 3px #ff7b00 !important;
    border-radius: 3px;
}

nav {
	background-color: #242424;
	overflow: hidden;
	padding: 1em;
    border-bottom: 1px solid #242424;
}


.navWide {
    display: none;
    margin: 0 auto;
}

.Profile-small {
    height: 6vmin;
    border-radius: 8vmin;
    float: left;
    border: solid 2px white;
}

.wideDiv {
    text-align: right;
}

.wideDiv Link {
    text-decoration: none;
    display: inline-block;
    padding: 0 2em;
}

.navNarrow .FaBars {
    float: right;
    cursor: pointer;
    color: rgb(255, 255, 255);
}

.navNarrow .narrowLinks {
    display: none;
}

.navNarrow .narrowLinks li {
        text-decoration: none;
        display: block;
        float: right;
        clear: right;
        padding: 0.5em 0;
        color: white;
        font-weight: bold;
}

.navNarrow .narrowLinks li:hover {
    cursor:pointer;
   }

@media (min-width: 600px) {
	
	nav .navWide {
		display: block;
    }

	nav	.navNarrow {
		display: none;
	}
}
